@use 'styles/colors';
@import 'styles/base';

:host {
  @include proxima(16px, 600);

  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: box-shadow 200ms linear;
  box-sizing: border-box;
  border-radius: 3px;
  cursor: pointer;
  letter-spacing: 0.05em;

  &:active, &.active {
    position: relative;
    top: 1px;
  }

  &[type="default"] {
    &[size="large"] {
      padding: 18px 32px;
      min-width: 157px;
      min-height: 56px;
    }

    &[size="medium"] {
      padding: 14px 24px;
      min-width: 158px;
      min-height: 48px;
    }

    &[size="small"] {
      padding: 10px 24px;
      min-width: 140px;
      min-height: 40px;
    }

    &[size="min"] {
      padding: 8px 14px;
    }
  }

  &[type="icon"] {
    flex-shrink: 0;
    border-radius: 50%;

    ::ng-deep .mat-icon {
      @include mat-icon-color(#FFFFFF);
    }

    &[size="large"] {
      height: 56px;
      width: 56px;

      ::ng-deep .mat-icon {
        @include mat-icon-size(32px);
      }
    }

    &[size="medium"] {
      height: 48px;
      width: 48px;

      ::ng-deep .mat-icon {
        @include mat-icon-size(28px);
      }
    }

    &[size="small"] {
      height: 40px;
      width: 40px;

      ::ng-deep .mat-icon {
        @include mat-icon-size(24px);
      }
    }

    &[size="min"] {
      height: 32px;
      width: 32px;

      ::ng-deep .mat-icon {
        @include mat-icon-size(20px);
      }
    }
  }



  &[color="red"] {
    color: white;
    background: linear-gradient(127.69deg, #FF3E14 6.56%, #FD852E 84.95%);
  }

  &[color="blue"] {
    color: white;
    background: linear-gradient(60.25deg, #0082CB 8.47%, #3DA2FF 91.53%);
  }

  &[color="green"] {
    color: white;
    background: linear-gradient(60.58deg, #21A34D 8.37%, #60DB56 91.63%);
  }

  &[color="gray"] {
    color: white;
    background: linear-gradient(60.25deg, #878787 8.47%, #aaaaaa 91.53%);
  }

  &[color="secondary"] {
    color: colors.$foreground_dark;
    background: #e7e7e7;

    &:hover {
      background: #E0E0E0;
    }
  }

  &[color="red-box"] {
    color: white;
    background: linear-gradient(127.69deg, #FF3E14 6.56%, #FD852E 84.95%);
    box-shadow: 0 10px 15px rgba(253, 133, 46, 0.35);

    &:hover {
      box-shadow: 0 10px 15px rgba(253, 133, 46, 0.15);
    }
  }

  &[color="blue-box"] {
    color: white;
    background: linear-gradient(60.25deg, #0082CB 8.47%, #3DA2FF 91.53%);
    box-shadow: 0 10px 15px rgba(61, 162, 255, 0.35);

    &:hover {
      box-shadow: 0 10px 15px rgba(61, 162, 255, 0.15);
    }
  }

  &[color="green-box"] {
    color: white;
    background: linear-gradient(60.58deg, #21A34D 8.37%, #60DB56 91.63%);
    box-shadow: 0 10px 15px rgba(96, 219, 86, 0.35);

    &:hover {
      box-shadow: 0 10px 15px rgba(96, 219, 86, 0.15);
    }
  }

  &[color="gray-box"] {
    color: white;
    background: linear-gradient(60.25deg, #878787 8.47%, #aaaaaa 91.53%);
    box-shadow: 0 10px 15px rgba(170, 170, 170, 0.35);

    &:hover {
      box-shadow: 0 10px 15px rgba(170, 170, 170, 0.15);
    }
  }


}
